import React, { useState, useEffect, useContext } from 'react';
import { View, Text, TextInput, StyleSheet } from 'react-native';
import { Json2WordContext } from '../Context/JSONContext';
function TextInputUpdater({ contextValue, setContextValue, label, key, run}) {
  const [localInputValue, setLocalInputValue] = useState(contextValue);
  const {JSON2Word,setJSON2Word, currentObjectIndex, currentRunIndex} = useContext(Json2WordContext)

  
  // useEffect(() => {
  //   if (key && JSON2Word && !run){
  //     js = JSON2Word;
  //     js[currentObjectIndex][key] = localInputValue;
  //     setJSON2Word(js);
  //   }
  //   else if (key && JSON2Word && run){

  //   }
  //   console.log(JSON2Word);
  //   setContextValue(localInputValue);
  // }, [localInputValue]);
  useEffect(() => {
    setContextValue(localInputValue)
  });
  const handleInputChange = (text) => {
    setLocalInputValue(text);
  };

  return (
    <View style={styles.textContainer}>
      <View style={styles.textLiveUpdateContainer}>
        <Text style={styles.label}>{label}:</Text>
        <View style={styles.inputContainer}>
          <TextInput
            style={styles.input}
            value={localInputValue}
            onChangeText={handleInputChange}
            placeholder={label}
          />
        </View>
      </View>
    </View>
  );
}

const styles = StyleSheet.create({
  textContainer: {
    flexDirection: 'row',
    alignItems: 'center',
    marginBottom: 20,
  },
label: {
    fontSize: 18,
    fontWeight: 'bold',
    marginRight: 10,
  },
  inputContainer: {
    flexDirection: 'column',
  },
  input: {
    height: 40,
    borderColor: 'gray',
    borderWidth: 1,
    flex: 1,
  },
  textLiveUpdateContainer: {
    flexDirection: 'row',
  },
  liveUpdate: {
    marginTop: 5,
  },
});

export default TextInputUpdater;
