import React, { useState, useEffect } from 'react';
import { View, Text, StyleSheet } from 'react-native';
import {Picker} from '@react-native-picker/picker';
function DropdownUpdater({ contextValue, setContextValue, label, options }) {
  const [localDropdownValue, setLocalDropdownValue] = useState(contextValue);

  useEffect(() => {
    setContextValue(localDropdownValue);
  }, [localDropdownValue]);

  const handleDropdownChange = (itemValue) => {
    setLocalDropdownValue(itemValue);
  };

  return (
    <View style={styles.container}>
      <Text style={styles.label}>{label}:</Text>
      <View style={styles.dropdownContainer}>
        <Picker
          selectedValue={localDropdownValue}
          onValueChange={handleDropdownChange}
        >
          {options.map((option, index) => (
            <Picker.Item label={option} value={option} key={index} />
          ))}
        </Picker>
      </View>
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flexDirection: 'row',
    alignItems: 'center',
    marginBottom: 20,
  },
  label: {
    fontSize: 18,
    fontWeight: 'bold',
    marginRight: 10,
  },
  dropdownContainer: {
    flexDirection: 'column',
  },
});

export default DropdownUpdater;
