import React, { useState, useEffect } from 'react';

export const Json2WordContext = React.createContext();

const Json2WordContextProvider = ({ children }) => {
    const [JSON2Word, setJSON2Word] = useState([]);
    const [currentObjectIndex, setCurrentObjectIndex] = useState(0);
    const [currentRunIndex, setCurrentRunIndex] = useState(0);
  useEffect( () => {
    setCurrentObjectIndex(currentObjectIndex);
    setCurrentRunIndex(currentRunIndex);
  })
  return (
    <Json2WordContext.Provider value={{
        JSON2Word,setJSON2Word,
        currentObjectIndex, setCurrentObjectIndex,
        currentRunIndex,setCurrentRunIndex
    }}>
      {children}
    </Json2WordContext.Provider>
  );
}

export default Json2WordContextProvider;
