import React, { useState, useEffect, useContext } from 'react';
import { View, TextInput, Button, ScrollView } from 'react-native';
import { Json2WordContext } from '../Context/JSONContext';
function AutoSizeTextAreaWithSave({ initialText }) {
  const {JSON2Word,setJSON2Word, currentObjectIndex} = useContext(Json2WordContext); 
  const [text, setText] = useState(JSON2Word[currentObjectIndex]);

  useEffect(() => {
    setText(JSON.stringify(JSON2Word[currentObjectIndex],null,1))
  }, [currentObjectIndex, JSON2Word])
  useEffect(() => {
    // Adjust the height of the TextInput dynamically based on content.
  }, [text]);

  const handleSaveClick = () => {
    const js = JSON.parse(text);
    console.log('Text saved:', js);
    const json2word = [...JSON2Word];
    json2word[currentObjectIndex] = js;
    setJSON2Word(json2word);
  };

  return (
    <View>
      <TextInput
        multiline
        value={text ? text:""}
        onChangeText={(newText) => setText(newText)}
        style={{ height: '40vw',width: '40vw', borderColor: 'gray', borderWidth: 1 }}
      />
      <Button title="Save" onPress={handleSaveClick} />
    </View>
  );
}

export default AutoSizeTextAreaWithSave;
