import React, { useState, useEffect, useContext } from 'react';
import { View, Text,  StyleSheet,  Pressable,TextInput } from 'react-native';
import { JSONContext } from '../Context/ParagraphContext';
import { Json2WordContext } from '../Context/JSONContext';
import TextInputUpdater from '../Components/TextInputUpdater';
import CheckboxUpdater from '../Components/CheckboxUpdater';
import DropdownUpdater from '../Components/DropDownUpdater';

function ParagraphForm(JSONObject) {
  const {JSON2Word,setJSON2Word,currentObjectIndex, currentRunIndex} = useContext(Json2WordContext);
  const [JsonState, setJsonState] = useState({...JSONObject}.JSONObject);
  useEffect(() => {
    const json2 = [...JSON2Word];
    json2[currentObjectIndex].Paragraph = {...JsonState};
    setJSON2Word([...json2]);
  },[currentObjectIndex,JsonState]);

  if (JSON2Word[currentObjectIndex] !== undefined && Object.keys(JSON2Word[currentObjectIndex])[0] === 'Paragraph'){
  return (
    <View style={styles.container}>
      <Text style={styles.heading}>Paragraph:</Text>
      <CheckboxUpdater 
        contextValue={JsonState.keep_with_next}
        setContextValue={(val) => {const js = {...JsonState};js.keep_with_next=val;setJsonState({...js})}}
        label="Keep with next"
        JsonKey="keep_with_next"
      />
      <CheckboxUpdater 
        contextValue={JsonState.border_active}
        setContextValue={(val) => {const js = {...JsonState};js.border_active=val;setJsonState({...js})}}
        label="Border"
        JsonKey="border_active"
      />
        <Text>{JSON.stringify(JsonState,null,2)}</Text>
      <View style={{...styles.borderGroupView, display:JSON2Word[currentObjectIndex].Paragraph.border_active ? 'flex':'none'}}>
      <Text>Hello new view.</Text>
  </View>
    {/* <Pressable onPress={handleParagraphSetter} style={({ pressed }) => [styles.button, { opacity: pressed ? 0.6 : 1 }]}>
      <Text style={styles.buttonText}>Save Paragraph</Text>
    </Pressable> */}
    <Text>currentObjectIndex: {currentObjectIndex}<br />
    currentRunIndex: {currentRunIndex}
    </Text>
    </View>
  
  )}
}
const styles = StyleSheet.create({
  heading:{
    fontSize:26,
    fontWeight:'bold',
    marginBottom:'2vw'
  },
  container: {

    justifyContent:'center',

  },
  button: {
    backgroundColor: 'blue', // Change to your preferred button color
    padding: 10,
    borderRadius: 5,
    alignItems: 'center',
    justifyContent: 'center',
  },
  buttonText: {
    color: 'white', // Change to your preferred text color
    fontSize: 16,
    fontWeight: 'bold',
  },
  borderGroupView: {
    marginLeft:"2vw",
    
  },
  styleGroupView: {
    marginLeft:"2vw",
    
  },
  textContainer: {
    flexDirection: 'row',
    alignItems: 'center',
    marginBottom: 20,
  },
label: {
    fontSize: 18,
    fontWeight: 'bold',
    marginRight: 10,
  },
  inputContainer: {
    flexDirection: 'column',
  },
  input: {
    height: 40,
    borderColor: 'gray',
    borderWidth: 1,
    flex: 1,
  },
  textLiveUpdateContainer: {
    flexDirection: 'row',
  },
  liveUpdate: {
    marginTop: 5,
  },


});
export default ParagraphForm;
