import React, {useContext,useState,useEffect} from 'react';
import { Json2WordContext } from '../Context/JSONContext';
import JSONDownload from './FileDownload';
import {View,TextInput} from 'react-native';
const NonEditableTextBox = () =>{
    const {JSON2Word} = useContext(Json2WordContext);
    const text = JSON.stringify(JSON2Word,null,2);

    return (
        <View>
        <JSONDownload />
      <TextInput
        multiline
        value={text}
        editable={false}
        style={{ height: '40vw',width: '40vw', borderColor: 'gray', borderWidth: 1 }}
      />
      
    </View>
    );
  
}

export default NonEditableTextBox;
