import React, {useContext} from 'react';
import { View, StyleSheet, Text } from 'react-native';
import { Json2WordContext } from '../Context/JSONContext';

const Json2WordDebugger = () => {
    const {JSON2Word,currentObjectIndex} = useContext(Json2WordContext);
    return (<View>
        <Text>
            {JSON.stringify(JSON2Word, null,2)};
        </Text>
    </View>)
}
export default Json2WordDebugger;

const styles = StyleSheet.create({
    container:{
        width:('20vw')
    },


})