// MyContextProvider.js
import React, { useState } from 'react';

export const JSONContext = React.createContext();

const ParagraphContext = ({ children }) => {
    const [PKeepWithNext, setPKeepWithNext] = useState(false);
    const [PBorderActive,setPBorderActive] = useState(false);
    const [PBorderColor,setPBorderColor] = useState('');
    const [PBorderArea,setPBorderArea] = useState('');
    const [PBorderSize,setPBorderSize] = useState('');
    const [PBorderType,setPBorderType] = useState('');
    const [PStyleName,setPStyleName] = useState('');
    const [PStyleActive,setPStyleActive] = useState('');
    const [PStyleFontActive,setPStyleFontActive] = useState('');
    const [PStyleFontSize,setPStyleFontSize] = useState('');
    const [PStyleFontBold,setPStyleFontBold] = useState('');
    const [PStyleFontName,setPStyleFontName] = useState('');
    const [PStyleFontItalic,setPStyleFontItalic] = useState('');
    const [PStyleFontUnderline,setPStyleFontUnderline] = useState('');
    const [PAlignment,setPAlignment] = useState('');






    const [Paragraph,setParagraph] = useState({});
  
  return (
    <JSONContext.Provider value={{
      PKeepWithNext,setPKeepWithNext,
    PBorderColor,setPBorderColor,
    PBorderActive,setPBorderActive,
    Paragraph,setParagraph,
    PBorderArea,setPBorderArea,
    PBorderSize,setPBorderSize,
    PBorderType,setPBorderType,
    PStyleName, setPStyleName,
    PStyleFontActive,setPStyleFontActive,
    PStyleActive,setPStyleActive,
    PStyleFontSize,setPStyleFontSize,
    PStyleFontBold,setPStyleFontBold,
    PStyleFontName,setPStyleFontName,
    PStyleFontItalic,setPStyleFontItalic,
    PStyleFontUnderline,setPStyleFontUnderline,
    PAlignment,setPAlignment
    
    
    }}>
      {children}
    </JSONContext.Provider>
  );
}

export default ParagraphContext;
