import React, { useState, useContext, useEffect } from 'react';
import { View, Text, FlatList, Pressable, StyleSheet } from 'react-native';
import { Json2WordContext } from '../Context/JSONContext';

const RunViewer = () => {
  const [selectedButton, setSelectedButton] = useState(null); // State to track the selected button
  const {currentRunIndex, setCurrentRunIndex} = useContext(Json2WordContext);
  const data = ['Button 1', 'Button 2', 'Button 3', 'Button 4', 'Button 5'];
  useEffect(() => {
    setCurrentRunIndex(selectedButton);
}, [selectedButton]);
  const handleButtonPress = (index) => {
    
    setSelectedButton(index);
    
  };

  const renderButton = ({ item, index }) => (
    <Pressable
      style={[
        styles.button,
        selectedButton === index ? styles.selectedButton : null,
      ]}
      onPress={() => handleButtonPress(index)}
    >
      <Text>{item}</Text>
    </Pressable>
  );

  return (
    <View style={styles.container}>
      <FlatList
        data={data}
        horizontal={true}
        renderItem={renderButton}
        keyExtractor={(item, index) => index.toString()}
      />
      {selectedButton !== null && (
        <Text style={styles.selectedText}>
          Selected button index: {selectedButton}<br />
          Selected currentRunIndex: {currentRunIndex}
        </Text>
      )}
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
  },
  button: {
    backgroundColor: 'blue',
    height:'5vw',
    justifyContent:'center',
    padding: 10,
    margin: 5,
    borderRadius: 5,
  },
  selectedButton: {
    backgroundColor: 'green', // Change the style for the selected button
  },
  selectedText: {
    marginTop: 10,
    fontSize: 18,
  },
});

export default RunViewer;
