import React, { useState, useContext, useEffect } from 'react';
import { View, Text, FlatList, Pressable, StyleSheet } from 'react-native';
import { Json2WordContext } from '../Context/JSONContext';

const ObjectViewer = () => {
  const [selectedButton, setSelectedButton] = useState(null); // State to track the selected button
  const {JSON2Word, setJSON2Word,currentObjectIndex, setCurrentObjectIndex} = useContext(Json2WordContext);
  const data = JSON2Word.map(record => Object.keys(record)[0]);

  useEffect(() => {
    setCurrentObjectIndex(selectedButton);
}, [selectedButton]);

  const handleButtonPress = (index) => {
    
    setSelectedButton(index);
    
  };
  const handleRemoveButtonPress = () => {
    const js = [...JSON2Word];
    js.splice(currentObjectIndex,1);
    console.log("Length: ",JSON2Word.length, " " , js.length);
    setCurrentObjectIndex(js.length-1);
    setJSON2Word(js);
    console.log(JSON.stringify(JSON2Word,null,2));
  }

  const renderButton = ({ item, index }) => (
    <Pressable
      style={[
        styles.button,
        currentObjectIndex === index ? styles.selectedButton : null,
      ]}
      onPress={() => handleButtonPress(index)}
    >
      <Text>{item}</Text>
    </Pressable>
  );

  return (
    <View style={styles.container}>
      <FlatList
        data={data}
        horizontal={true}
        renderItem={renderButton}
        keyExtractor={(item, index) => index.toString()}
        selectedButton={currentObjectIndex}

      />
       <Pressable
            style={styles.removeButton}
            onPress={() => handleRemoveButtonPress()}
          >
            <Text style={styles.removeButtonText}>X</Text>
          </Pressable>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    justifyContent: 'center',
    flexDirection:'row',
    alignItems: 'center',
  },
  removeButtonText:{
    backgroundColor: 'red',
    padding: 10,
    height:'3vw',
    justifyContent:'center',
    alignItems:'center',
    margin: 5,
    borderRadius: 5,
    userSelect:'none'
  },
  button: {
    backgroundColor: 'blue',
    padding: 10,
    height:'3vw',
    justifyContent:'center',
    margin: 5,
    borderRadius: 5,
  },
  selectedButton: {
    backgroundColor: 'lightblue', // Change the style for the selected button
  },
  selectedText: {
    marginTop: 10,
    fontSize: 18,
  },
});

export default ObjectViewer;
