import React, { useState, useEffect,useContext } from 'react';
import { View, Text, Switch, StyleSheet } from 'react-native';
import { Json2WordContext } from '../Context/JSONContext';
function CheckboxUpdater({ contextValue, setContextValue, label, JsonKey }) {
  const [local, setLocal] = useState(contextValue);
  const {JSON2Word,currentObjectIndex} = useContext(Json2WordContext);

  useEffect(() => {

    setContextValue(local);
  
}, [local]
  );
  useEffect(() => {

    setLocal(contextValue);
  
}, [contextValue]
  );
  useEffect(() => {
    // Your code to run when contextValue changes
    setLocal(JSON2Word[currentObjectIndex].Paragraph[JsonKey]);
    console.log(JSON2Word[currentObjectIndex].Paragraph[JsonKey]);
    console.log(local);
  }, [currentObjectIndex]);

  //
  const toggleCheckbox = (val) => {
    setLocal(val);
    console.log(JSON2Word[currentObjectIndex].Paragraph[JsonKey] ? 'True':'False');
  };

  return (
    <View style={styles.container}>
      <Text style={styles.label}>{label}:</Text>
      <View style={styles.checkboxContainer}>
        <Switch
          value={local}
          onValueChange={toggleCheckbox}
        />
        <Text>Value:{JSON2Word[currentObjectIndex].Paragraph[JsonKey] ? 'True':'False'} </Text>
      </View>
    </View>
  );
}

const styles = StyleSheet.create({
  container: {
    flexDirection: 'row',
    alignItems: 'center',
    marginBottom: 20,
  },
  label: {
    fontSize: 18,
    fontWeight: 'bold',
    marginRight: 10,
  },
  checkboxContainer: {
    flexDirection: 'column',
  },
});

export default CheckboxUpdater;
