import React, {useContext} from 'react';
import { Json2WordContext } from '../Context/JSONContext';

const JSONDownload = ({ jsonData }) => {
    const {JSON2Word} = useContext(Json2WordContext);

    const downloadJSON = () => {
        const jsonContent = JSON.stringify({"Json2Word":JSON2Word}, null, 2);
        const blob = new Blob([jsonContent], { type: 'application/json' });

        // Create a download link
        const link = document.createElement('a');
        link.href = window.URL.createObjectURL(blob);
        link.setAttribute('download', 'data.json'); // Set the desired file name
        console.log(link);
        // Trigger a click event on the download link to start the download
        document.body.appendChild(link);
        link.click();

        // Clean up after the download link is clicked
        document.body.removeChild(link);
    };

  return (
    <div>
      <button onClick={downloadJSON}>Download JSON</button>
    </div>
  );
};

export default JSONDownload;
