import React, { useState, useEffect, useContext} from 'react';
import { View, Text } from 'react-native';
import { RadioButton, Button } from 'react-native-paper';
import { Json2WordContext } from '../Context/JSONContext';

const MyComponent = () => {
  const [selectedOption, setSelectedOption] = useState(null);
  
  const {JSON2Word,setJSON2Word,currentObjectIndex,setCurrentObjectIndex}= useContext(Json2WordContext);
  const [object,setObject] = useState(JSON2Word);
  const radioProps = [
    { label: 'Header', value: 'Header' },
    { label: 'Paragraph', value: 'Paragraph' },
    { label: 'Table', value: 'Table' },
    { label: 'Page break', value: 'Page_break' },
    { label: 'Footer', value: 'Footer' },
  ];
  useEffect(() => {
    if (object !== JSON2Word) {
      setObject(JSON2Word);
    }
  }, [currentObjectIndex])
  const handleAddObject = () => {
    console.log(object);
    // Add your logic here to perform an action when the "Add Object" button is pressed
    if (selectedOption==='Paragraph'){
    const js = {'Paragraph':{
      "keep_with_next":false,
      "border_active":false,
      "border_color":'#000000',
      "border_area":'Bottom',
      "border_size":1,
      "border_space":1,
      "border_type":'Single',
      "style_name":null,
      "style_font_active":true,
      "style_active":false,
      "style_font_size":14,
      "style_font_bold":false,
      "style_font_name":'Arial',
      "style_font_italic":false,
      "style_font_underline":false,
      "alignment":'left',
      "runs":[
        {
          "type":"text",
                "text": 'Examplerun',
                "run_font_active":false, 
                "run_font_size": 14,
                "run_font_bold": false,
                "run_font_name": 'Arial',
                "run_font_italic": false,
                "run_font_underline": false,
                "hyperlink_active":true,
                "hyperlink_url":"https://thepodium.com.ph/wp-content/uploads/2019/07/1-2.jpg",
                "color": [0, 0, 0],
                "image_content":"https://thepodium.com.ph/wp-content/uploads/2019/07/1-2.jpg",
                "image_width":1.5,
                "checkbox_checked":false,
                "checkbox_size":20
        }
      ]
    }};
    setObject([...object,js]);
    }
    else if (selectedOption==='Page_break'){
      const js = {'Page_break':{}};
    setObject([...object,js]);
    
    }
    else if (selectedOption==='Table'){
      const js = {'Table':{
        "params": {
            
          "Image_properties": {
            "columns": ["image_column1", "image_column2"],
            "image_size": [120, 120]
          },
          "column_widths": [1, 1.5,4],
          "preset": "preset_style",
          "paragraph": {
            "paragraph_style": "Colorful List Accent 1",
            "font": {
              "size": 12,
              "name": "Arial",
              "bold": true,
              "italic": false,
              "underline": true
            }
          },
          "table_style": "Table Grid",
          "columns": ["column1",  "column3"],
          "header_row": {
              'height': 0.4,
              'background-color': [50, 100, 150],
              'font-size': 12,
              'bold': true,
              'italic': false,
              'underline': false
          },
        },
        "items": [
          {
            "column1": "Apple",
            "column2": "Red",
            "column3": "Fruit"
          },
          {
            "column1": "Banana",
            "column2": "Yellow",
            "column3": "Fruit"
          },
          {
            "column1": "Carrot",
            "column2": "Orange",
            "column3": "Vegetable"
          }
      ]
      }}
      setObject([...object,js]);
    }
    else if (selectedOption==='Footer') {
    const js = {"Footer":{
      "paragraphs":[
        {
          "keep_with_next":false,
          "border_active":false,
          "border_color":'#000000',
          "border_area":'Bottom',
          "border_size":1,
          "border_type":'Single',
          "style_name":null,
          "style_font_active":true,
          "style_active":false,
          "style_font_size":14,
          "style_font_bold":false,
          "style_font_name":'Arial',
          "style_font_italic":false,
          "style_font_underline":false,
          "alignment":'left',
          "runs":[
            {
              "type":"text",
                    "text": 'Example footer',
                    "run_font_active":false, 
                    "run_font_size": 14,
                    "run_font_bold": false,
                    "run_font_name": 'Arial',
                    "run_font_italic": false,
                    "run_font_underline": false,
                    "hyperlink_active":true,
                    "hyperlink_url":"https://thepodium.com.ph/wp-content/uploads/2019/07/1-2.jpg",
                    "color": [0, 0, 0],
                    "image_content":"https://thepodium.com.ph/wp-content/uploads/2019/07/1-2.jpg",
                    "image_width":1.5,
                    "checkbox_checked":false,
                    "checkbox_size":20
            }
          ]
        },
      ]

    }}


      setObject([...object,js]);
    }
    else if (selectedOption==="Header"){
      const js = {"Header":{
        "paragraphs":[
          {
            "keep_with_next":false,
            "border_active":false,
            "border_color":'#000000',
            "border_area":'Bottom',
            "border_size":1,
            "border_type":'Single',
            "style_name":null,
            "style_font_active":true,
            "style_active":false,
            "style_font_size":14,
            "style_font_bold":false,
            "style_font_name":'Arial',
            "style_font_italic":false,
            "style_font_underline":false,
            "alignment":'left',
            "runs":[
              {
                "type":"text",
                "text": 'Example footer',
                "run_font_active":false, 
                "run_font_size": 14,
                "run_font_bold": false,
                "run_font_name": 'Arial',
                "run_font_italic": false,
                "run_font_underline": false,
                "hyperlink_active":true,
                "hyperlink_url":"https://thepodium.com.ph/wp-content/uploads/2019/07/1-2.jpg",
                "color": [0, 0, 0],
                "image_content":"https://thepodium.com.ph/wp-content/uploads/2019/07/1-2.jpg",
                "image_width":1.5,
                "checkbox_checked":false,
                "checkbox_size":20
              }
            ]
          },
        ]
  
      }}
      setObject([...object,js]);
    }
  };
  useEffect(() => {
    setJSON2Word(object);
    setCurrentObjectIndex(JSON2Word.length);
  }, [object]);
  return (
    <View>
      <Text style={{ fontSize: 24 }}>Json2Word</Text>
      <Text style={{ fontSize: 16 }}>Add new objects</Text>

      <View style={{ flexDirection: 'row', alignItems: 'center' }}>
        {radioProps.map((radio, index) => (
          <View key={index} style={{ flexDirection: 'row', alignItems: 'center', marginRight: 16 }}>
            <RadioButton
              value={radio.value}
              status={selectedOption === radio.value ? 'checked' : 'unchecked'}
              onPress={() => setSelectedOption(radio.value)}
            />
            <Text>{radio.label}</Text>
          </View>
        ))}
      </View>

      <Button mode="contained" onPress={handleAddObject} style={{userSelect:"none"}}>
        Add Object
      </Button>
    </View>
  );
};

export default MyComponent;
